:root {
    --black: #1c1818;
    --white: #f5f5f5;
    --red: #d45341;
}

/* CSS specific to iOS devices */
a {
    text-decoration: none;
    color: var(--black);
}

.light {
    padding-left: 5px;
}

.placeholder-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100%;
}

.heading-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
    margin-bottom: 70px;
    padding: 5.05vw 0 0 0;
}

.heading-container-resp {
    display: none;
}

.main-heading {
    font-size: 20vw;
    letter-spacing: 0.6vw;
    color: var(--black);
    white-space: nowrap;
    height: 15.2vw;
    /* background: #d45341; */
}
#sub-heading {
    background-color: var(--white);
    position: absolute;
    padding: 0.625vw 10px 0.46875vw 10px;
    font-size: 1.14vw;
    color: var(--red);
}

.sub-info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 90%;
    margin-left: 5.2vw;
}

.info-question {
    font-size: 1.04vw;
    letter-spacing: 0.8px;
    font-weight: 100;
}

.info-answer {
    padding-top: 0.78vw;
    font-size: 1.56vw;
    color: var(--red);
    letter-spacing: 1.2px;
    line-height: 21px;
}

.coming-soon {
    margin-top: 7vw;
    font-size: 2.6vw;
    letter-spacing: 2px;
    color: var(--black);
}
.footer {
    margin-top: 6vw;
    padding-bottom: 20px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.footer-style {
    display: flex;
    justify-content: center;
}

.social-icons {
    margin: 7.5px;
}
.footer-email {
    font-size: 1.56vw;
    letter-spacing: 1.2px;
}
svg {
    width: 100%;
    height: 100%;
}

/* ----------- iPad Pro 12.9" ----------- */

/* Portrait and Landscape */
@media only screen and (min-device-width: 768px) and (max-device-width: 1366px) and (orientation: Portrait) {
    .heading-container {
        display: none;
    }

    .heading-container-resp {
        display: flex;
        flex-direction: column;
        width: 100%;
        margin-top: 70px !important;
    }
    .sub-resp-cont {
        width: 100%;
        overflow: hidden;
    }
    .main-heading-resp1 {
        font-size: 170pt;
        height: 170px;
        position: relative;
        white-space: nowrap;
        margin-bottom: 100px;
        letter-spacing: 4.2pt;
    }

    .main-heading-resp2 {
        font-size: 170pt;
        height: 170px;
        position: relative;
        white-space: nowrap;
        margin-bottom: 120px;
        letter-spacing: 4.2pt;
    }

    .main-heading-resp2:after,
    .main-heading-resp1:after {
        border-top: 28px solid var(--white);
        position: absolute;
        content: "";
        right: 0;
        top: 42%;
        left: 0;
    }

    #sub-heading-resp {
        display: block;
        width: 2000pt;
        font-size: 18pt;
        margin-bottom: 40px;
        color: var(--red);
        padding-top: 5px;
    }
    .scrolling {
        position: relative;
        -webkit-animation: scroll 16s infinite linear;
        -moz-animation: scroll 16s infinite linear;
        -o-animation: scroll 16s infinite linear;
        animation: scroll 16s infinite linear;
    }
    @keyframes scroll {
        0% {
            left: 300px;
        }
        100% {
            left: -1900px;
        }
    }
    @-webkit-keyframes scroll {
        0% {
            left: 300px;
        }
        100% {
            left: -1900px;
        }
    }
    .sub-info {
        margin-top: 150px !important;
        width: 85% !important;
    }

    .info-question {
        font-size: 20px !important;
    }

    .info-answer {
        padding-top: 8px !important;
        font-size: 22px !important;
        text-align: left !important;
    }

    .coming-soon {
        margin-top: 170px !important;
        font-size: 50px !important;
    }
    .footer {
        margin-top: 150px !important;
        padding-bottom: 20px !important;
    }
    .footer-email {
        font-size: 22px !important;
    }
}

/* Phones */
@media only screen and (max-device-width: 450px) {
    .heading-container {
        display: none;
    }

    .heading-container-resp {
        display: flex;
        flex-direction: column;
        width: 100%;
        margin-top: 15vw;
    }
    .sub-resp-cont {
        width: 100%;
        overflow: hidden;
    }
    .main-heading-resp1 {
        font-size: 80pt;
        height: 80px;
        position: relative;
        white-space: nowrap;
        margin-bottom: 60px;
        letter-spacing: 4.2pt;
    }

    .main-heading-resp2 {
        font-size: 80pt;
        height: 80px;
        position: relative;
        white-space: nowrap;
        margin-bottom: 70px;
        letter-spacing: 4.2pt;
    }

    .main-heading-resp2:after,
    .main-heading-resp1:after {
        border-top: 14px solid var(--white);
        position: absolute;
        content: "";
        right: 0;
        top: 42%;
        left: 0;
    }

    #sub-heading-resp {
        display: block;
        width: 2000px;
        font-size: 16pt;
        margin-bottom: 40px;
        color: var(--red);
    }
    .scrolling {
        position: relative;
        -webkit-animation: scroll 30s infinite linear;
        -moz-animation: scroll 30s infinite linear;
        -o-animation: scroll 30s infinite linear;
        animation: scroll 30s infinite linear;
    }
    @keyframes scroll {
        0% {
            left: 300px;
        }
        100% {
            left: -1900px;
        }
    }
    @-webkit-keyframes scroll {
        0% {
            left: 300px;
        }
        100% {
            left: -1900px;
        }
    }
    .sub-info {
        width: 80%;
        margin-left: 0vw;
        margin-top: 80px;
    }
    .info-question {
        font-size: 10pt;
        letter-spacing: 0.8px;
        font-weight: 100;
    }

    .info-answer {
        padding-top: 12px;
        font-size: 12pt;
        color: var(--red);
        letter-spacing: 1.2px;
        text-align: left;
    }

    .coming-soon {
        margin-top: 80px;
        font-size: 20.5pt;
        letter-spacing: 1.08pt;
        color: var(--black);
    }
    .footer {
        margin-top: 80px;
        padding-bottom: 5px;
        flex-direction: column;
        width: 100%;
        position: none;
        bottom: none;
        left: none;
    }
    .social-icons {
        margin: 6.5px;
        width: 35px;
    }
    .footer-style {
        display: flex;
        justify-content: center;
    }
    .resp-footer {
        display: none;
    }
    .footer-email {
        margin-top: 4px;
        font-size: 13pt;
        letter-spacing: 0.5pt;
    }
}

/* iphone x */
@media only screen and (min-device-width: 375px) and (max-device-width: 812px) and (-webkit-min-device-pixel-ratio: 3) {
    .heading-container {
        margin-top: 4vw;
    }
    #sub-heading {
        margin-top: 30px;
    }
    .sub-info {
        width: 90%;
        margin-left: 10px;
        margin-top: 80px;
    }
    .footer {
        margin-top: 65px;
    }
}

/* iphone 11/11 pro max */
@media only screen and (min-device-width: 414px) and (max-device-width: 896px) {
    .heading-container {
        margin-top: 4vw;
    }

    .sub-info {
        width: 90%;
        margin-left: 10px;
        margin-top: 130px;
    }
    .coming-soon {
        margin-top: 95px;
    }
}
